import code from "../../util/code.js"
import common from "../../util/common.js"
export default {
	data() {
		return {
			telInfo: '', //手机号
			examNameInfo: '', //考试信息
			startTime: '', //开始时间
			endTime: '', //结束
			examState: '' //考试状态
		}
	},
	onLoad(option) {
		var markN = option.mark;//获取网址带的mark
		// var markN = "2bffd3458d6443dabe2e7740f949cdb1";
		var markO = uni.getStorageSync("markS");// 获取本地的marks
		var loginStaV = uni.getStorageSync("loginStateS");//目前的登录状态
		var userIdV = uni.getStorageSync("userIdS");//获取用户的id
		var sUserAgent = navigator.userAgent.toLowerCase();
		//跳转移动端页面
		if (markN == "" || markN == undefined || markN == null || markN == NaN || markN == 'NaN') {
			uni.reLaunch({
				url: "../four/four"
			})
			uni.clearStorage();
		} else {
			if (/ipad|iphone|midp|rv:1.2.3.4|ucweb|android|windows ce|windows mobile/.test(sUserAgent)) {
				if (markN == markO) { //已经已经点击过一次了
					console.log("已经执行过一次")
					this.telInfo = uni.getStorageSync("telS");
					this.examNameInfo = uni.getStorageSync("examNameS");
				}else{
					console.log("第一执行");
					uni.clearStorage();
					uni.setStorageSync("markS", markN);
					if (loginStaV == "") {
						uni.setStorageSync("loginStateS", 0);
					}else{
						if (userIdV == "") {
							return;
						} else {
							uni.setStorageSync("loginStateS", "1");
							uni.setStorageSync("userIdS", userIdV);
						}
					}
					//获取用户信息
					uni.request({
						url: common.port.mMark,
						header: {
							'content-type': 'application/x-www-form-urlencoded',
						},
						method: "POST",
						dataType: "json",
						data: {
							mark: markN
						},
						success: (result) => {
							if (result.data.code == 0) {
								this.telInfo = result.data.data.phone;
								this.examNameInfo = result.data.data.exam_name;
								uni.setStorageSync("telS", result.data.data.phone); //手机号
								uni.setStorageSync("examNameS", result.data.data.exam_name); //考试标题
								uni.setStorageSync("examIdS", result.data.data.exam_id); //试卷id
								uni.setStorageSync("authorityS", result.data.data.authority); //权限
								uni.setStorageSync("timeModeS", result.data.data.time_mode); //答题时间
								uni.setStorageSync("startS", result.data.data.start_time); //开始时间
								uni.setStorageSync("endS", result.data.data.end_time); //结束时间
								uni.setStorageSync("durationS", result.data.data.duration); //时长
								uni.setStorageSync("answerCS", result.data.data.answer_count); //答题次数
								this.startTime = result.data.data.start_time //开始时间
								this.endTime = result.data.data.end_time //结束时间
								uni.setStorageSync("radioScoreS", result.data.data.radio_score); //单选题分值
								uni.setStorageSync("checkScoreS", result.data.data.check_score); //多选题分值
								uni.setStorageSync("judgeScoreS", result.data.data.judge_score); //判断题分值
								uni.setStorageSync("fillScoreS", result.data.data.fill_score); //填空题分值
							}else {
								uni.showToast({
									title: result.msg,
									icon: 'none',
									duration: 2000,
									success: function() {
										uni.reLaunch({
											url: "../four/four"
										})
										uni.clearStorage();
									}
								})
							}
						}
					})
				}
			}else{
				//跳转pc端页面
				window.location.href="http://tests.sdrhup.com/dist-p/#/?mark=" + markN;
			}
		}
		
	},
	methods: {
		// 加入游戏
		joinExam: function() {
			uni.showLoading({
				title: '加载中...',
			})
			var userIdV = uni.getStorageSync("userIdS");//用户id
			var loginSV = uni.getStorageSync("loginStateS");//是否登录
			var gradeS = uni.getStorageSync("gradeS");//成绩
			var authorityV = uni.getStorageSync("authorityS");//是否需要密码
			var pwdV = uni.getStorageSync("pwdstartS");
			var examidV = uni.getStorageSync("examIdS");//考试id
			if (userIdV == "" || userIdV == undefined || userIdV == null || userIdV == NaN || userIdV == 'NaN' ||
				loginSV == "" || loginSV == undefined || loginSV == null || loginSV == NaN || loginSV == 'NaN') {
				uni.navigateTo({
					url: "../login/login"
				})
			} else {
				//已经登录过了并且存在用户id
				// 判断是否存在成绩，如果存在则直接到成绩页面
				if(gradeS == ""){//分数为空
					uni.request({
						url: common.port.lastLog,
						header: {
							'content-type': 'application/x-www-form-urlencoded',
						},
						method: "POST",
						dataType: "json",
						data: {
							exam_id: examidV,
							user_id: userIdV
						},
						success: (result) => {
							uni.hideLoading();
							if(result.score>0){//分数大于0
								uni.navigateTo({
									url:'../grade/grade'
								})
							}else{
								if(authorityV == 2){//密码答题
									if(pwdV == 1 ){//表示密码登录过了
										uni.navigateTo({
											url:"../notice/notice"
										})
									}else{
										uni.navigateTo({
											url:"../pwd/pwd"
										})
									}
								}else{
									uni.navigateTo({
										url:"../notice/notice"
									})
								}
							}
						},
					})
				}else{//分数不为空
					uni.navigateTo({
						url:'../grade/grade'
					})
				}
			}
		}	
	}
}
